.results-container {
  margin: 0 20px
}
.results-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.result-item {
  flex: 0 1 20vw;
  max-width: 200px;
  margin: 12px;
  border-bottom: 1px solid gray;
  display: flex;
  flex-direction: column;  
  align-items: center;
  justify-content: space-between;
}


.result-item img {
  width: 100%;
  height: 300px;  
  background-color: lemonchiffon;
  min-width: 200px;
}

